export default {
	data() {
		return {
			value: '',
			list: [],
			// 控制是否显示
			active: false,
			activeIndex: 0,
			text: '全部'
		}
	},
	computed: {
		listData() {
			// 将数组转为字符串以后再转为对象
			// 获取全部数据
			let list = JSON.parse(JSON.stringify(this.list))
			// 得到筛选过后的数据
			let newList = []
			// 点击 全部
			if (this.activeIndex === 0) {
				this.text = '全部'
				return list
			}
			// 点击 待办
			if (this.activeIndex === 1) {
				// checked = false
				list.forEach((item) => {
					if (!item.checked) {
						newList.push(item)
					}
				})
				this.text = '待办'
				return newList
			}
			// 点击 已完成
			if (this.activeIndex === 2) {
				if (this.activeIndex === 2) {
					// checked = true
					list.forEach((item) => {
						if (item.checked) {
							newList.push(item)
						}
					})
					this.text = '已完成'
					return newList
				}
			}
		}
	},
	onLoad() {},
	methods: {
		// 打开输入框
		create() {
			// 显示输入框
			this.active = true
		},
		// 添加待办事项
		add() {
			console.log(this.value)
			// 如果输入框为空 弹出提示
			if (this.value === '') {
				uni.showToast({
					title: "请输入内容",
					icon: "none"
				})
				return
			}
			// 将输入数据放到list数组中
			this.list.unshift({
				// 给list每个内容加一个时间戳
				id: 'id' + new Date().getTime(),
				content: this.value,
				checked: false
			})
			// 置空输入框
			this.value = ''
			// 将输入框隐藏
			this.active = false
		},
		// 点击列表触发
		finish(id) {
			// 找到当前点击的列表所在行
			console.log("我被点击了", id)
			let index = this.list.findIndex((item) => item.id === id)
			console.log("当前点击的行", this.list[index])
			this.list[index].checked = !this.list[index].checked
		},
		tab(index) {
			this.activeIndex = index
		}
	}
}
